.cover-container {
    height: 100vh;
    width: 100%;
    object-fit: contain;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.6);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.cover-container > h1 {
    text-align: center;
    color: white;
    font-size: 100px;
    align-items: center;
}

.cover-container > p {
    margin-top: 8px;
    color: white;
    font-size: 32px;
}

.img-cover {
    object-fit: cover;
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: -1;

}

@media screen and (max-width: 960px) {

    .cover-container > h1 {
        font-size: 40px;
    }

    .cover-container > p {
        text-align: center;
        font-size: 22px;
        width: 85%;
    }
}


